<template>
<div>
<img src="title.JPG" style="margin-top:100px;width:300px;height:80px"><br>
<strong style="font-size:2em" v-if="status=='join'"></strong>
<strong style="font-size:2em" v-if="status=='mod'"></strong>
<div class="check_first" v-if="status=='join'">
<form action="" id="joinForm">
            <ul class="join_box">                
            <li class="checkBox check02">
                    <li style="margin-top:30px;font-weight: bold;">  ()</li>
                    <textarea readonly style="width:1000px; height:100px; resize:none; border:2px solid #DCDFE6;">< >('login.jinong.co.kr' '     ')(   .)()                     .
< >('     ') ()      ( )   .
       </textarea>
            <el-checkbox style="margin-left:730px; font-weight:bold" v-model="info_law_checked" id="info_law_checkbox"> () .</el-checkbox>
           <li class="checkBox check03">
                    <li style="font-weight: bold;">   ()</li>
                    <textarea readonly style="width:1000px; height:100px; resize:none; border:2px solid #DCDFE6;"> 1  ()
       (   )    (  )          .
                   </textarea>
                        <el-checkbox style="margin-left:680px;font-weight:bold" v-model="homepage_law_checked" id="homepage_law_checkbox">   () .</el-checkbox>
                    <br>
                    <br>
                    <el-checkbox style="margin-left:490px;font-weight:bold"  @change="selectAll" v-model="all_law_checked" id="all_law_checkbox"> (),    ()  .</el-checkbox>
                    </ul>
         </form>
</div>
<div class="input_form">
<div class="input_table">
<table>
<tr>
<th>
 <label for="id"></label><br>
  <input type="profile" id="id" v-model="user.userid" value="user.userid">
</th>
<th>
   <label for="name"></label><br>
   <input type="profile" id="name" v-model="user.name">
</th>
</tr>
<tr>
<th>
   <label for="password"></label><br>
   <input type="password" id="password" v-model="user.password">
</th>
<th>
   <label for="password_check"></label><br>
   <input type="password" id="password_check" v-model="password_check">
</th>
</tr>
<br>
<tr>
<th>
   <label for="phone"></label><br>
   <input type="text" id="phone" v-model="user.phone">
</th>
<th>
  <label for="email">e</label><br>
   <input type="text" id="email" v-model="user.email">
</th>
</tr>
<tr>
<th>
  <label for="group"></label><br>
   <input type="text" id="group" v-model="user.group">
   <br><br><br>
</th>
<th>
  <label for="device"></label><br>
   <input type="text" id="device" placeholder="" v-model="user.dev"><br>
   <input  type="checkbox" id="checkbox" v-model="checked">
<label style="font-size:1em; " for="checkbox"></label>
</th>
</tr>
</table>
</div> 
<br>
<el-button type="primary" style="width:100px;height:40px;font-size:1em;" v-on:click="createAccount" v-if="status=='join'"></el-button>
<el-button type="primary" style="width:100px;height:40px;font-size:1em;" v-on:click="modAccount" v-if="status=='mod'"></el-button>
<el-button type="danger" style="width:100px;height:40px;font-size:1em;" v-on:click="delAccount" v-if="status=='mod'"></el-button>
<el-button type="default" style="width:100px;height:40px;font-size:1em;" v-on: @click="$router.go(-1)"></el-button>
</div>
</div>
</template>
<script>
export default {
    data: function () {
  return {
    user: {
      userid: '',
      name: '',
      password: '',
      phone: '',
      email: '',
      group:'',
      dev:'',
      law_check:'',
      same:''
    },
    homepage_law_checked:false,
    info_law_checked:false,
    all_law_checked:false,
    password_check:''
    ,checked:''
    ,status:''
    ,form:this.$route.query
  }
},
  mounted() {
    console.log("?")
    console.log(this.homepage_law_checked)
    this.check_status();
      },
   methods: {
  createAccount: function () {
    if(this.homepage_law_checked==true&&this.info_law_checked==true){
     this.user.law_check=true;
    if(this.checked==true){
      this.user.dev="";
    }
    console.log(this.checked)
    console.log(this.user.dev)
    if(this.user.userid==''){
      alert(" .");
      return;
    }
    if(this.user.name==''){
      alert(" .");
      return;
    }
    if(this.user.password==''){
      alert(" .");
      return;
    }
    if(this.user.phone==''){
      alert(" .");
      return;
    }
    if(this.user.email==''){
      alert("e-mail .");
      return;
    }
    if(this.user.dev==''){
      alert("   .");
      return;
    }
    if(this.user.password==this.password_check){
    this.$http.post('/users/createAcc', { 
      user: this.user
    })
    .then((res) => {
      if (res.data.createsuccess == true) {
        alert(res.data.message);
        this.$router.go(-1);
      }
      if (res.data.createsuccess == false) {
        alert(res.data.message);
        this.user.userid='',
        this.user.name='',
        this.user.password='',
        this.user.phone='',
        this.user.email='',
        this.user.law_check='',
        this.user.dev=''
      }
    })
    .catch(function (error) {
      alert(error)
      this.user.userid='',
        this.user.name='',
        this.user.password='',
        this.user.phone='',
        this.user.email='',
        this.user.law_check='',
        this.user.dev=''
    })
    }
    else{
    alert(' .');
  }
  }
    else{
      alert(' .');  
  }
  },
  modAccount:function(){
    this.user.law_check=true;
     if(this.checked==true){
      this.user.dev="";
    }
    if(this.user.userid==''){
      alert(" .");
      return;
    }
    if(this.user.name==''){
      alert(" .");
      return;
    }
    if(this.user.password==''){
      alert(" .");
      return;
    }
    if(this.user.phone==''){
      alert(" .");
      return;
    }
    if(this.user.email==''){
      alert("e-mail .");
      return;
    }
    if(this.user.dev==''){
      alert("   .");
      return;
    }
    var id=localStorage.getItem("id");
    
    if(id==this.user.userid){
      this.user.same=true
    }
    else {
      this.user.same=false
    }
    if(this.user.password==this.password_check){
    this.$http.post('/users/modAcc', { 
      user: this.user,
      before_mod_id:localStorage.getItem("id")
    })
    .then((res) => {
      if (res.data.success == true) {
        alert(res.data.message);
        localStorage.setItem("id",res.data.id);
        this.$router.go(-1);
      }
      if (res.data.success == false) {
        alert(res.data.message);
      }
    })
    .catch(function (error) {
      alert(error)
    })
    }
    else{
      alert(" . .")
    }
  }
  ,
  selectAll:function(){
    console.log("")
    console.log(this.all_law_checked+"?")
    if(this.all_law_checked==true)
    {
      this.homepage_law_checked=true;
      this.info_law_checked=true;
    }
    else{
      this.homepage_law_checked=false;
      this.info_law_checked=false;    
    }
  },
  check_status:function(){
    var id=localStorage.getItem("id");
    if(id==null){
      this.status='join';
    }
    else if(id!=null){
      this.status='mod';
      var body={
        id:id
      }
      this.$http.post('/users/check_user_info',body)
			.then((res)=>{
				if(res.data.success) {
          for(var key in this.form){
            this.user.password=this.user.password+this.form[key]
          }
        this.user.userid=res.data.data[0].id,
        this.user.name=res.data.data[0].user_name,
        this.user.phone=res.data.data[0].phone,
        this.user.password_check=this.user.password,
        this.user.email=res.data.data[0].email,
        this.user.group=res.data.data[0].user_group,
        this.user.dev=res.data.data[0].user_device
        
          console.log('2'+this.user.password)
				} else {
					alert(" .\n  ");
				}
			})
			.catch((err)=>{
				console.log(err);
			})
    }
  }
  ,
  delAccount:function(){
    this.$http.post('/users/delAcc', { 
      id:localStorage.getItem("id")
    })
    .then((res)=>{
				if(res.data.success) {
           localStorage.removeItem("id");
           localStorage.removeItem("auth");
          alert(res.data.message);
           this.$router.go(-1);
        } else {
					alert(" .\n  ");
				}
			})
			.catch((err)=>{
				console.log(err);
			})
  }
}
}
</script>
<style scoped>
body{background-color: #f7f7f7;}
ul>li{list-style: none}
a{text-decoration: none;}
#joinForm{width: 70%;margin: 0 auto; height:450px; }
ul.join_box{border: 1px solid #ddd;height:400px;background-color: #fff;}
.check_first{width:100%;position: relative;}
.input_form{position: relative;width:800;height:900px;margin-left:30%;margin-right:30%;}
.input_table{margin-top:30px;text-align:left;width:700px;height: 520px;border-radius: 5px;border: 1px solid #DCDFE6;}
.input_table table{margin-top:0px;}
.input_table label{font-size:1.4em; font-weight: bold;color:#606266;}
.input_table input{font-size:1.3em;}
.input_table input[type=profile]{width:300px;height: 40px;border-radius: 10px;border: 1px solid #DCDFE6;}
.input_table input[type=password]{width:300px;height: 40px;border-radius: 10px;border: 1px solid #DCDFE6;}
.input_table input[type=text]{width:300px;height: 40px;border-radius: 10px;border: 1px solid #DCDFE6;}
.input_table input[type=checkbox]{width:20px;height: 20px;border-radius: 10px;border: 1px solid #DCDFE6;}
.input_table tr th{padding:20px;}
</style>